import React, { useState, useEffect } from 'react';
import directionObject from '../waves/DirectionObject';

const categories = [
  'all',
  'temperature',
  'currency',
  'volume',
  'length',
  'ocean'
]
const units = {
  Celsius: ['Fahrenheit'],
  Fahrenheit: ['Celsius'],
  Gallons: ['Liters'],
  Liters: ['Gallons'],
  Pesos: ['Dollars'],
  Dollars: ['Pesos'],
  Miles: ['Kilometers', 'Feet'],
  Kilometers: ['Miles'],
  Inches: ['Centimeters', 'Feet'],
  Centimeters: ['Inches'],
  Pounds: ['Kilograms', 'Grams', 'Ounces'],
  Kilograms: ['Pounds', 'Grams', 'Ounces'],
  Ounces: ['Grams', 'Cups', 'Tablespoons', 'Teaspoons', 'Pounds', 'Kilograms'],
  Grams: ['Ounces', 'Cups', 'Tablespoons', 'Teaspoons', 'Kilograms', 'Pounds'],
  Cups: ['Ounces', 'Tablespoons', 'Teaspoons', 'Grams'],
  Tablespoons: ['Ounces', 'Teaspoons', 'Grams', 'Cups'],
  Teaspoons: ['Ounces', 'Tablespoons', 'Grams', 'Cups'],
  Feet: ['Meters', 'Inches', 'Miles'],
  Meters: ['Feet', 'Inches', 'Miles'],
  MPH: ['Knots'],
  Knots: ['MPH'],
  Degrees: ['Direction'],
  Direction: ['Degrees']
};

const temperature = {
  Celsius: ['Fahrenheit'],
  Fahrenheit: ['Celsius']
};

const currency = {
  Pesos: ['Dollars'],
  Dollars: ['Pesos']
};

const volume = {
  Gallons: ['Liters'],
  Liters: ['Gallons'],
  Pounds: ['Kilograms', 'Grams', 'Ounces'],
  Kilograms: ['Pounds', 'Grams', 'Ounces'],
  Ounces: ['Grams', 'Cups', 'Tablespoons', 'Teaspoons', 'Pounds', 'Kilograms'],
  Grams: ['Ounces', 'Cups', 'Tablespoons', 'Teaspoons', 'Kilograms', 'Pounds'],
  Cups: ['Ounces', 'Tablespoons', 'Teaspoons', 'Grams'],
  Tablespoons: ['Ounces', 'Teaspoons', 'Grams', 'Cups'],
  Teaspoons: ['Ounces', 'Tablespoons', 'Grams', 'Cups']
};

const length = {
  Miles: ['Kilometers', 'Feet'],
  Kilometers: ['Miles'],
  Inches: ['Centimeters', 'Feet'],
  Centimeters: ['Inches'],
  Feet: ['Meters', 'Inches', 'Miles'],
  Meters: ['Feet', 'Inches', 'Miles']
};

const ocean = {
  MPH: ['Knots'],
  Knots: ['MPH'],
  Degrees: ['Direction'],
  Direction: ['Degrees']
};
const unitObjects = {
  all: units,
  temperature: temperature,
  currency: currency,
  volume: volume,
  length: length,
  ocean: ocean
}
const directions = Object.keys(directionObject);
const degrees = Object.values(directionObject);

const degreesToDirection = (degrees) => {
  const directionsAndDegrees = Object.entries(directionObject);
  for (let i = 0; i < directionsAndDegrees.length; i++) {
    if (degrees < directionsAndDegrees[i][1]) {
      return directionsAndDegrees[i - 1][0];
    }
  }
  return 'N';
};

const directionToDegrees = (direction) => {
  return directionObject[direction] !== undefined ? directionObject[direction] : 0;
};

const conversionFactors = {
  Celsius: (value) => (value * 9/5) + 32,
  Fahrenheit: (value) => (value - 32) * 5 / 9,
  Gallons: (value) => value * 3.78541,
  Liters: (value) => value / 3.78541,
  Pesos: (value) => value / 20, // example conversion rate
  Dollars: (value) => value * 20, // example conversion rate
  Miles: (value, toUnit) => toUnit === 'Kilometers' ? value * 1.60934 : value * 5280,
  Kilometers: (value) => value / 1.60934,
  Inches: (value, toUnit) => toUnit === 'Centimeters' ? value * 2.54 : value / 12,
  Centimeters: (value) => value / 2.54,
  Pounds: (value, toUnit) => {
    switch (toUnit) {
      case 'Kilograms':
        return value * 0.453592;
      case 'Grams':
        return value * 453.592;
      case 'Ounces':
        return value * 16;
      default:
        return value;
    }
  },
  Kilograms: (value, toUnit) => {
    switch (toUnit) {
      case 'Pounds':
        return value / 0.453592;
      case 'Grams':
        return value * 1000;
      case 'Ounces':
        return value * 35.274;
      default:
        return value;
    }
  },
  Ounces: (value, toUnit) => {
    switch (toUnit) {
      case 'Grams':
        return value * 28.3495;
      case 'Cups':
        return value / 8;
      case 'Tablespoons':
        return value * 2;
      case 'Teaspoons':
        return value * 6;
      case 'Pounds':
        return value / 16;
      case 'Kilograms':
        return value / 35.274;
      default:
        return value;
    }
  },
  Grams: (value, toUnit) => {
    switch (toUnit) {
      case 'Ounces':
        return value / 28.3495;
      case 'Cups':
        return value / 240;
      case 'Tablespoons':
        return value / 15;
      case 'Teaspoons':
        return value / 5;
      case 'Kilograms':
        return value / 1000;
      case 'Pounds':
        return value / 453.592;
      default:
        return value;
    }
  },
  Cups: (value, toUnit) => {
    switch (toUnit) {
      case 'Ounces':
        return value * 8;
      case 'Tablespoons':
        return value * 16;
      case 'Teaspoons':
        return value * 48;
      case 'Grams':
        return value * 240;
      default:
        return value;
    }
  },
  Tablespoons: (value, toUnit) => {
    switch (toUnit) {
      case 'Ounces':
        return value / 2;
      case 'Cups':
        return value / 16;
      case 'Teaspoons':
        return value * 3;
      case 'Grams':
        return value * 15;
      default:
        return value;
    }
  },
  Teaspoons: (value, toUnit) => {
    switch (toUnit) {
      case 'Ounces':
        return value / 6;
      case 'Cups':
        return value / 48;
      case 'Tablespoons':
        return value / 3;
      case 'Grams':
        return value * 5;
      default:
        return value;
    }
  },
  Feet: (value, toUnit) => {
    switch (toUnit) {
      case 'Meters':
        return value * 0.3048;
      case 'Inches':
        return value * 12;
      case 'Miles':
        return value / 5280;
      default:
        return value;
    }
  },
  Meters: (value, toUnit) => {
    switch (toUnit) {
      case 'Feet':
        return value / 0.3048;
      case 'Inches':
        return value * 39.3701;
      case 'Miles':
        return value / 1609.34;
      default:
        return value;
    }
  },
  MPH: (value) => value * 0.868976,
  Knots: (value) => value / 0.868976,
  Direction: (value) => directionToDegrees(value),
  Degrees: (value, toUnit) => {
    const closestDirection = directions.reduce((prev, curr) => 
      Math.abs(directionObject[curr] - value) < Math.abs(directionObject[prev] - value) ? curr : prev
    );
    return toUnit === 'Direction' ? closestDirection : value;
  },
  Direction: (value, toUnit) => {
    if (value !== NaN) {
      return toUnit === 'Degrees' ? directionObject[value] : value;
    }
  }
};

const Converter = () => {
  const [category, setCategory] = useState('all');
//  alert(`unit1: ${Object.keys(unitObjects[category])[0]}`);
  const [unit1, setUnit1] = useState(Object.keys(unitObjects[category])[0]);
//  alert(`unit2: ${units[Object.keys(unitObjects[category])[0]][0]}`);
  const [unit2, setUnit2] = useState(units[Object.keys(unitObjects[category])[0]][0]);
  const [value1, setValue1] = useState('');
  const [value2, setValue2] = useState('');

  useEffect(() => {
    setUnit1(Object.keys(unitObjects[category])[0]);
    setUnit2(units[Object.keys(unitObjects[category])[0]][0]);
  }, [category]);

  useEffect(() => {
    setUnit2(units[unit1][0]);
    if (value1) {
      if (unit1 === 'Direction' || unit2 === 'Direction') {
        setValue2(conversionFactors[unit1](parseFloat(value1), unit2));
      } else {
        setValue2(conversionFactors[unit1](parseFloat(value1), unit2).toFixed(2));
      }
    }
  }, [unit1]);

  useEffect(() => {
    if (value2) {
      if (unit1 === 'Direction' || unit2 === 'Direction') {
        setValue1(conversionFactors[unit2](parseFloat(value2), unit1));
      } else {
        setValue1(conversionFactors[unit2](parseFloat(value2), unit1).toFixed(2));
      }
    }
  }, [unit2]);

  const handleValue1Change = (e) => {
    const newValue = e.target.value;
    setValue1(newValue);
    if (unit1 === 'Degrees') {
      setValue2(conversionFactors[unit1](parseFloat(newValue), unit2));
    } else if (unit1 === 'Direction') {
      setValue2(conversionFactors[unit1](newValue, unit2));
    } else {
      setValue2(conversionFactors[unit1](parseFloat(newValue), unit2).toFixed(2));
    }
  };

  const handleValue2Change = (e) => {
    let newValue = null;
    if (unit2 === 'Degrees') {
      newValue = e;
      setValue2(newValue);
      setValue1(conversionFactors[unit2](parseFloat(newValue), unit1));
    } else if (unit2 === 'Direction') {
      newValue = e;
      setValue2(newValue);
      setValue1(conversionFactors[unit2](newValue, unit1));
    } else {
      newValue = e.target.value;
      setValue2(newValue);
      setValue1(conversionFactors[unit2](parseFloat(newValue), unit1).toFixed(2));
    }
  };

  return (
    <div>
      <div className='containerBox'>
        <div className='containerBox flexContainer bg-lite'>
          <div className='containerBox flex2Column contentRight'>
            Category:
          </div>
          <select className='containerBox flex2Column' value={category} onChange={(e) => setCategory(e.target.value)}>
            {categories.map((category) => (
              <option key={category} value={category}>
                {category}
              </option>
            ))}
          </select>
        </div>
      </div>
      <div className='containerBox'>
        <div className='containerBox flexContainer bg-lite'>
          <select className='containerBox flex2Column contentRight' value={unit1} onChange={(e) => setUnit1(e.target.value)}>
            {Object.keys(unitObjects[category]).map((unit) => (
              <option key={unit} value={unit}>
                {unit}:
              </option>
            ))}
          </select>
          <div className='flex2Column pr-10'>
            {
              (unit1 === 'Degrees')
              ? <select className='containerBox flex2Column width-100-percent' value={value1} onChange={handleValue1Change}>
                  {degrees.map((degree) => (
                    <option key={degree} value={degree}>
                      {degree}
                    </option>
                  ))}
                </select>
              : (unit1 === 'Direction')
                ? <select className='containerBox flex2Column width-100-percent' value={value1} onChange={handleValue1Change}>
                    {directions.map((direction) => (
                      <option key={direction} value={direction}>
                        {direction}
                      </option>
                    ))}
                  </select>
                : <input 
                    className='containerBox bg-dark width-100-percent' 
                    type="number" 
                    value={value1} 
                    onChange={handleValue1Change} 
                  />
            }
          </div>
        </div>
        <div className='containerBox flexContainer bg-lite'>
          {
            (unitObjects[category][unit1] !== undefined)
            ? <select className='containerBox flex2Column contentRight' value={unit2} onChange={(e) => setUnit2(e.target.value)}>
                  {
                    unitObjects[category][unit1].map((unit) => (
                      <option key={unit} value={unit}>
                        {unit}:
                      </option>
                    ))
                  }
              </select>
            : <div className='containerBox bg-neogreen'>
              {/*unitObjects[category][unit1][0]*/}
              catergory: {category} unit1: {unit1}
            </div>
          }
          <div className='flex2Column pr-10'>
          {
              (unit2 === 'Degrees')
              ? <select className='containerBox flex2Column width-100-percent' value={value2} onChange={(e) => handleValue2Change(e.target.value)}>
                  {degrees.map((degree) => (
                    <option key={degree} value={degree}>
                      {degree}
                    </option>
                  ))}
                </select>
              : (unit2 === 'Direction')
                ? <select className='containerBox flex2Column width-100-percent' value={value2} onChange={(e) => handleValue2Change(e.target.value)}>
                    {directions.map((direction) => (
                      <option key={direction} value={direction}>
                        {direction}
                      </option>
                    ))}
                  </select>
                : <input 
                    className='containerBox bg-dark width-100-percent' 
                    type="number" 
                    value={value2} 
                    onChange={handleValue2Change} 
                  />
            }
          </div>
        </div>
      </div>
    </div>
  );
};

export default Converter;